
.cus-side[data-v-61693d58]{
   position: fixed;
   top: 0;
   left: 0;
   bottom: 0;
   z-index: 20000;
   /*height: 100%;*/
   padding: 0;
   overflow-y: auto;
   /*pointer-events: none*/
}
.cus-bar[data-v-61693d58]{
   /* 要位移需要宽度 */
   width: 300px;
   min-height: 100%;
   height: auto;
   padding: 0;
   background-color: #fff;
   font-size: 18px;
   line-height: 1.42;
   color: #fff;
   overflow-y: auto;
}
.cus-user[data-v-61693d58]{
   background-color: #00a2ed;
}
.cus-user img[data-v-61693d58]{
   width: 40px;
   height: 40px;
}
.cus-user>div[data-v-61693d58]{
   height: 60px;
   line-height: 50px;
   padding: 10px 20px;
}
.cus-user i[data-v-61693d58]{
   margin-right: 10px;
}
.cus-user a[data-v-61693d58]{
   color: #fff;
   text-decoration: none;
}
.cus-bar h3[data-v-61693d58]{
   color: #00a2ed;
   margin: 0;
   padding: 20px;
   background-color: #f3f3f3;
   font-size: 16px;
}
.cus-bar h3 a[data-v-61693d58]{
   text-decoration: none;
}
.cus-bar h3 i[data-v-61693d58]{
   margin-right: 20px;
}
.cus-channel[data-v-61693d58]{
   margin-bottom: 20px;
}
.cus-channel span[data-v-61693d58]{
   margin-left: 10px;
   font-size: 16px;
   color: #222;
}
.cus-channel i[data-v-61693d58]{
   color: #bbb;
}

 /* 夜间模式 */
.night .cus-user[data-v-61693d58]{
   background-color: #222;
}
.night .cus-user a[data-v-61693d58],
 .night .cus-user span[data-v-61693d58]{
   color: #999;
}
.night .cus-user i[data-v-61693d58]{
   color: #fff;
}
.night .cus-bar h3[data-v-61693d58]{
   background-color: #333;
}
.night .cus-channel[data-v-61693d58],
 .night .cus-item[data-v-61693d58]{
   background-color: #555;
   border: none;
}
.night .cus-bar[data-v-61693d58]{
   background-color: #555;
   /*border: none;*/
}
.night .cus-channel span[data-v-61693d58]{
   color: #aaa;
   font-weight: 400;
}

/* 隐藏滚动条 */
[data-v-61693d58]::-webkit-scrollbar{
   background-color: transparent;
   width: 0;
}




