
.cus-loading[data-v-9b66fc10]{
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.loading[data-v-9b66fc10]{
  position: relative;
  width: 100px;
  height: 20px;
  margin: 200px auto;
}
.loading i[data-v-9b66fc10]{
  position:absolute;
  display:inline-block;
  width:20px;
  height:20px;
  border-radius:50%;
  background:#333;
  margin-right:10px;
}
.loading i[data-v-9b66fc10]:nth-child(1){
  background-color: #909e9f;
  -webkit-animation:loading 2s linear 0s infinite;
          animation:loading 2s linear 0s infinite;
}
.loading i[data-v-9b66fc10]:nth-child(2){
  background-color: #34bc98;
  -webkit-animation:loading 2s linear -0.4s infinite;
          animation:loading 2s linear -0.4s infinite;
}
.loading i[data-v-9b66fc10]:nth-child(3){
  background-color: #fe6553;
  -webkit-animation:loading 2s linear -0.8s infinite;
          animation:loading 2s linear -0.8s infinite;
}
.loading i[data-v-9b66fc10]:nth-child(4){
  background-color: #fee79b;
  -webkit-animation:loading 2s linear -1.2s infinite;
          animation:loading 2s linear -1.2s infinite;
}
.loading i[data-v-9b66fc10]:nth-child(5){
  background-color: #f6a0bb;
  -webkit-animation:loading 2s linear -1.6s infinite;
          animation:loading 2s linear -1.6s infinite;
}
/* loading 动画 */
@-webkit-keyframes loading {
0%{
    left:100px;
    top:0;
}
80%{
    left:0;
    top:0;
}
85%{
    left:0;
    top:-20px;
    width:20px;
    height:20px;
}
90%{
    width:40px;
    height:20px;
}
95%{
    left:100px;
    top:-20px;
    width:20px;
    height:20px;
}
100%{
    left:100px;
    top:0;
}
}
@keyframes loading {
0%{
    left:100px;
    top:0;
}
80%{
    left:0;
    top:0;
}
85%{
    left:0;
    top:-20px;
    width:20px;
    height:20px;
}
90%{
    width:40px;
    height:20px;
}
95%{
    left:100px;
    top:-20px;
    width:20px;
    height:20px;
}
100%{
    left:100px;
    top:0;
}
}



